@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir-Heavy.ttf);
  font-style: normal;
  font-weight: 900;
}

html {
  font-family: "Avenir", Helvetica, sans-serif;
}

* {
  box-sizing: border-box;
}

.image {
  display: block;
  position: absolute;
  height: 19px;
  width: 19px;
  margin-top: 25px;
  margin-left: 26px;
}

.search-bar {
  display: block;
  width: 100%;
  height: 70px;
  margin-top: 20px;
  padding-left: 62px;
  font-size: 16px;
  font-weight: 300;
  font-family: inherit;
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
}

.image--small {
  height: 11px;
  width: 11px;
  margin-top: 15px;
  margin-left: 13px;
}

.search--small {
  height: 42px;
  font-size: 14px;
  font-weight: 400;
  padding-left: 33px;
}

.search-bar::placeholder {
  color: #3d4e61;
}

.search-bar:hover {
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
  border-radius: 4px;
}

.search-bar:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  outline: none;
}
